      .nav_right img{
        width: 100%;
        margin-top: .5rem;
        margin-left: .2rem;
        position: relative;
        z-index: 999;
      }

      .pickShow {
        position: absolute;
        top: .5rem;
        left: 14.2rem;
        z-index: 1;
      }

    }

  }

</style>



