<template>
  <div class="homepage" id="homepage">
    <div class="nav">
      <div class="nav_left"  @click="goHome()">
        <div style="float: left"><img src="../../assets/image/earth.gif" alt="" class="ico_earth"></div>
        <div style="float: left"><img src="../../assets/image/icon_03.png" alt="" class="ico_earth_a"></div>
      </div>
      <div class="nav_right" >
        <img src="../../assets/image/icon_05.png" alt="" class="ico_pick" @click="goPick(index)">
      </div>
    </div>
    <div class="swiper">
      <div class="swiper-container" >
        <div class="swiper-wrapper">
          <div class="swiper-slide " id="mao">
            <div class="slide" @click="ShowDetails1(index)"><img src="../../assets/image/1-2-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetails2(index)"><img src="../../assets/image/1-2-2.png" alt=""  class="bottom"></div>
          </div>
          <div class="swiper-slide">
            <div class="slide" @click="ShowDetails3(index)"><img src="../../assets/image/3-4-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetails4(index)"><img src="../../assets/image/3-4-2.png" alt=""  class="bottom"></div>
          </div>
          <div class="swiper-slide">
            <div class="slide" @click="ShowDetails5(index)"><img src="../../assets/image/5-6-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetails6(index)"><img src="../../assets/image/5-6-2.png" alt=""  class="bottom"></div>
          </div>
          <div class="swiper-slide">
            <div class="slide" @click="ShowDetails7(index)"><img src="../../assets/image/7-8-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetails8(index)"><img src="../../assets/image/7-8-2.png" alt=""  class="bottom"></div>
          </div>
          <div class="swiper-slide">
            <div class="slide" @click="ShowDetails9(index)"><img src="../../assets/image/9-10-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetails10(index)"><img src="../../assets/image/9-10-2.png" alt=""  class="bottom"></div>
          </div>
          <div class="swiper-slide">
            <div class="slide" @click="ShowDetaila(index)"><img src="../../assets/image/11-12-1.png" alt=""  class="top"></div>
            <div class="slide" @click="ShowDetailb(index)"><img src="../../assets/image/11-12-2.png" alt=""  class="bottom"></div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import details from '../details/details.vue'
  import pick from '../pick/pick.vue'
  import $ from 'jquery'
export default {
  name: 'homepage',
  data () {
    return {
      index:Number,
      indexa:this.$route.query.index_
    }
  },

  created(){
    this.indexa= this.$route.query.index_
    console.log(this.indexa)
  },

  mounted(index){
//    window.android.setScreenOrientation(0)

    var that = this
    var mySwiper = new Swiper ('.swiper-container', {
      direction: 'horizontal',
//      loop: true,
      click: true,
      tap: true, //或者
      preventClicks : false,
      watchState: true,
      zoom : {
        maxRatio: 3,
      },
      normalizeSlideIndex:false,
      // 如果需要分页器
      pagination: '.swiper-pagination',
      // 如果需要前进后退按钮
      nextButton: '.swiper-button-next',
      prevButton: '.swiper-button-prev',
      // 如果需要滚动条
      scrollbar: '.swiper-scrollbar',
      on:{
        slideChange: function(){
          that.index = mySwiper.realIndex
        },
      },
    })
    console.log(that.indexa)
    mySwiper.slideTo(that.indexa);

  },

  methods: {
    goHome(){
      if( typeof( goofypapaGame ) != "undefined" && goofypapaGame ){
        window.location.href='goofypapa://back';
      }else{
        window.android.exitApp()
      }
    },

    goPick(index){
      this.$router.push({
        path: '/pick',
        component: pick,
        query: {index:index}
      })

    },
    ShowDetails1(index){
        console.log(index)
      var that =this
      this.$router.push({
        path: '/details',
        component: details,
        query: {index:index,name: "1shuiquan"}
      })

      console.log(123)
    },
    ShowDetails2(index){
      console.log(index)
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "2shengwuquan",index:index}
          })

          console.log(456)
    },
    ShowDetails3(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "3gushengwu",index:index}
          })

          console.log(456)
    },
    ShowDetails4(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "4dizhizuoyong",index:index}
          })

          console.log(456)
    },
    ShowDetails5(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "5dizhiniandai",index:index}
          })

