    },
    ShowDetails7(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "7diqiuqiyuan",index:index}
          })

          console.log(456)
    },
    ShowDetails8(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "8diqiuneibu",index:index}
          })

          console.log(456)
    },
    ShowDetails9(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "9diqiugouzao",index:index}
          })

          console.log(456)
    },
    ShowDetails10(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "10daqiquan",index:index}
          })

          console.log(456)
    },
    ShowDetaila(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "11zirandimao",index:index}
          })

          console.log(456)
    },
    ShowDetailb(index){
          this.$router.push({
            path: '/details',
            component: details,
            query: {name: "12dimaoxianxiang",index:index}
          })

          console.log(456)
    },


  },
  components:{
    details,pick
  }
}
