<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


  .nav{
    height: 1.87rem;
    width: 100%;
    display: flex;
    justify-content:space-around;
  }

  .homepage{
    width: 100%;
    height: 100%;
    background: url("../../assets/image/background.png");
    background-size:cover;
  }

  .ico_earth{
    width: 1rem;
    height: 1rem;
    margin-top: .5rem;
    float: left;
  }
  .ico_earth_a{
    width: 1rem;
    height: .5rem;
    float: left;
    margin-top: .8rem;
    margin-left: 0.1rem;
  }
  .ico_pick{
    width: .9rem;
    height: .9rem;
    margin-top: .6rem;
    margin-left: 7rem;
  }
  .swiper{
    width: 100%;
    height:  7.15rem;
  }
  .swiper-container {
    width: 100%;
    height: 7.15rem;
  }
  .swiper-slide img{
    width: 5rem;
    height: 5.4rem;
  }

  .swiper-slide .top{
    margin-top:.7rem;
    margin-left: 1.6rem;
  }
  .swiper-slide {
    display: flex;
    flex-direction: row;
  }
  .swiper-slide .bottom{
    margin-top: .7rem;
    margin-left: 2.8rem;
  }

  .slide span{
    text-align: center;
    font-size: .7rem;
  }

  @media only screen and (device-width: 375px) and (device-height: 812px) and
  (-webkit-device-pixel-ratio: 3) {
    /*增加头部适配层*/
    .homepage[data-v-2a87c4e2] {
      width: 100%;
      height: 100%;
      background: url("../../assets/image/background.png");
      background-size: 100% 100%;
    }
    .nav{
      height: 1.87rem;
      width: 100%;
      display: flex;
      justify-content: space-around;
    }

    .ico_earth{
      width: .8rem;
      height: .8rem;
      margin-top: .5rem;
      float: left;
    }
    .ico_earth_a {
      width: 1rem;
      height: .45rem;
      float: left;
      margin-top: .65rem;
      margin-left: 0.1rem;
    }
    .ico_pick{
      width: .8rem;
      height: .8rem;
      margin-top: .5rem;
      margin-left: 7rem;
    }
    .swiper{
      width: 100%;
      height: 100%;
    }
    .swiper-container {
      width: 100%;
      height: 100%;
    }
    .swiper-slide .top{
      margin-top: .3rem;
      margin-left: 1.9rem;
    }
    .swiper-slide img {
      width: 4rem;
      height: 4.4rem;
    }
    .swiper-slide .bottom {
      margin-top: .3rem;
      margin-left: 4.08rem;
    }
    .swiper-wrapper{
      margin-bottom: .45rem;
    }
  }

</style>



