import Vue from 'vue'
import Router from 'vue-router'
import homepage from '@/components/homepage/homepage'
import details from '@/components/details/details'
import pickDetails from '@/components/pickDetail/pickDetails'
import pick from '@/components/pick/pick'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homepage',
      component: homepage
    },
    {
      path: '/details',
      name: 'details',
      component: details
    },
    {
      path: '/pick',
      name: 'pick',
      component: pick
    },
   {
      path: '/pickDetails',
      name: 'pickDetails',
      component: pickDetails
    }

  ]
})



